import React from "react";
import { withStyles } from "@material-ui/core";
import ColorPicker from "@hhsdev/react-color-picker";
import AboutMeBackground from "./background.jsx";
import AboutMeDescContainer from "./descContainer.jsx";

const styles = {
  root: {
	position: "relative",
    display: "flex",
    justifyContent: "center",
  },
  picker: {
	display: "inline-block",
	position: "absolute",
	left: 10,
	top: 10,
    zIndex: 1,
    background: "#FFF",
  }
};

class AboutMeContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      height: 0,
      mouseX: null,
      mouseY: null,
      ballColor: [0, 0, 0],
      backColor: [0, 0, 0]
    };
  }

  componentDidMount() {
    this.updateHeight();
    //window.addEventListener("resize", () => this.updateHeight());
    this.div.addEventListener("mousemove", e => {
      this.mouseMoved(e);
      return false;
    });
  }

  updateHeight() {
    this.setState({ height: window.innerHeight });
  }

  mouseMoved(e) {
    this.setState({
      mouseX: e.clientX,
      mouseY: e.clientY
    });
  }

  setBallColor(r, g, b) {
    this.setState({
      ballColor: [r, g, b]
    });
  }
  setBackColor(r, g, b) {
    this.setState({
      backColor: [r, g, b]
    });
  }

  render() {
    return (
      <div
        className={this.props.classes.root}
        ref={div => (this.div = div)}
        style={{ height: this.state.height }}
      >
        <AboutMeBackground
          backColor={this.state.backColor}
          height={this.state.height}
          mouseX={this.state.mouseX}
          mouseY={this.state.mouseY}
        />
        <AboutMeDescContainer />
        <ColorPicker className={this.props.classes.picker} callback={(r, g, b) => { this.setBackColor(r, g, b)}} />
      </div>
    );
  }
}

export default withStyles(styles)(AboutMeContainer);
