import React from "react";
import * as PIXI from "pixi.js";

class AboutMeBackground extends React.Component {
  constructor(props) {
    super(props);
    //this.app = new PIXI.Application({width: 300, height: 300, antialias: true });
    //this.app.renderer.autoResize = true;
    // these enable touch scrolling
    //this.app.renderer.plugins.interaction.autoPreventDefault = false;
    //this.app.renderer.view.style.touchAction = 'auto';
  }

  componentDidMount() {
    //this.div.appendChild(this.app.view);
    //this.renderBackground();
    this.offset = 0;
  }

  componentDidUpdate() {
    //const backColor = PIXI.utils.rgb2hex(this.props.backColor.map(v => v / 256));
    //this.app.renderer.backgroundColor = backColor;
    //this.app.renderer.resize(this.div.clientWidth, this.div.clientHeight);
    //this.renderBackground();
  }

  renderBackground() {
    const width = this.div.clientWidth;
    const height = this.div.clientHeight;
    this.app.stage.removeChildren();
    for (let x = 20; x <= width + 40; x += 120) {
      for (let y = 20; y <= height + 40; y += 120) {
        const circle = new PIXI.Graphics();
        const fillColor = PIXI.utils.rgb2hex(this.props.ballColor.map(v => v / 256));
        circle.beginFill(fillColor);
        let distance = 40;
        if (this.props.mouseX !== null && this.props.mouseY !== null)
          distance = Math.hypot(width - x - this.props.mouseX, height - y - this.props.mouseY);
        circle.drawCircle(x + this.offset, y, distance / 16);
        this.app.stage.addChild(circle);
      }
    }
  }

  render() {
	const { r, g, b } = this.props.backColor[0];
	console.log({r, g, b});
    return (
      <div
        style={{
		  background: `rgb(${r}, ${g}, ${b})`,
          position: "absolute",
          width: "100%",
          height: this.props.height,
		  zIndex: -1,
        }}
      ></div>
    );
  }
}

export default AboutMeBackground;
